/* Loader */
.cf-loader {
  position: absolute;
  bottom: 30px;
  left: 0;
  right: 0;
  text-align: center;
  display: block;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -moz-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  -moz-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
}
.cf-loader:after {
  display: block;
  margin: auto;
  position: relative;
  width: 15px;
  height: 15px;
  -webkit-animation: rotate 0.6s linear infinite;
  -moz-animation: rotate 0.6s linear infinite;
  -ms-animation: rotate 0.6s linear infinite;
  -o-animation: rotate 0.6s linear infinite;
  animation: rotate 0.6s linear infinite;
  -webkit-border-radius: 100%;
  -moz-border-radius: 100%;
  border-radius: 100%;
  border-top: 2px solid #f68121;
  border-bottom: 2px solid #ffffff;
  border-left: 2px solid #f68121;
  border-right: 2px solid #ffffff;
  content: '';
}
@keyframes rotate {
  0% {
    transform: rotateZ(-360deg);
    -webkit-transform: rotateZ(-360deg);
    -moz-transform: rotateZ(-360deg);
    -o-transform: rotateZ(-360deg);
  }
  100% {
    transform: rotateZ(0deg);
    -webkit-transform: rotateZ(0deg);
    -moz-transform: rotateZ(0deg);
    -o-transform: rotateZ(0deg);
  }
}
@-webkit-keyframes rotate {
  0% {
    transform: rotateZ(-360deg);
    -webkit-transform: rotateZ(-360deg);
    -moz-transform: rotateZ(-360deg);
    -o-transform: rotateZ(-360deg);
  }
  100% {
    transform: rotateZ(0deg);
    -webkit-transform: rotateZ(0deg);
    -moz-transform: rotateZ(0deg);
    -o-transform: rotateZ(0deg);
  }
}
@-moz-keyframes rotate {
  0% {
    transform: rotateZ(-360deg);
    -webkit-transform: rotateZ(-360deg);
    -moz-transform: rotateZ(-360deg);
    -o-transform: rotateZ(-360deg);
  }
  100% {
    transform: rotateZ(0deg);
    -webkit-transform: rotateZ(0deg);
    -moz-transform: rotateZ(0deg);
    -o-transform: rotateZ(0deg);
  }
}
@-o-keyframes rotate {
  0% {
    transform: rotateZ(-360deg);
    -webkit-transform: rotateZ(-360deg);
    -moz-transform: rotateZ(-360deg);
    -o-transform: rotateZ(-360deg);
  }
  100% {
    transform: rotateZ(0deg);
    -webkit-transform: rotateZ(0deg);
    -moz-transform: rotateZ(0deg);
    -o-transform: rotateZ(0deg);
  }
}
/* Transition */
.ease {
  transition: all 0.3s ease-in-out 0.3s;
}
/* Back Button */
.breadcrumb-bar .bread-crumb-list {
  padding-bottom: 0;
}
.breadcrumb-bar .bread-crumb-list .brdcrmb-wrp li a {
  color: #f68121;
  font-weight: bold;
}
.breadcrumb-bar .bread-crumb-list .brdcrmb-wrp li a::before {
  content: "\e77a";
  font-family: icomoon;
}
/* Toggle Button */
.toggle-button {
  position: absolute;
  border: 1px solid #f68121;
  border-radius: 3px;
  background-color: #ffffff;
  color: #f68121;
  padding: 6px 12px;
  transform: rotate(270deg);
  left: -34px;
  top: 45%;
  cursor: pointer;
  z-index: 999;
  transition: all 0.3s ease-in-out 0.3s;
}
.toggle-button::before {
  content: "\e756";
  font-family: icomoon;
  margin-right: 5px;
}
.toggle-button:hover {
  background-color: #f68121;
  color: #ffffff;
}
.toggle-view-button {
  display: none;
}
@media (max-width: 767px) {
  .toggle-button {
    display: none;
  }
  /* Mobile toggle */
  .toggle-view-button {
    position: absolute;
    bottom: 10px;
    right: 20px;
    display: block;
    border: 1px solid #f68121;
    border-radius: 50px;
    background-color: #ffffff;
    color: #f68121;
    cursor: pointer;
    padding: 8px 13px;
    z-index: 999;
  }
  .toggle-view-button.map::before {
    content: "\e610";
    font-family: icomoon;
    font-size: 22px;
  }
  .toggle-view-button.list::before {
    content: "\e756";
    font-family: icomoon;
    font-size: 22px;
  }
  .toggle-view-button:hover {
    background-color: #f68121;
    color: #ffffff;
  }
  .us-right-content.mobile.visible,
  .us-left-content.mobile.visible {
    display: block;
  }
  .us-right-content.mobile.not-visible,
  .us-left-content.mobile.not-visible {
    display: none;
  }
}
/* Lazy Load Loader */
.lazy-hidden {
  opacity: 1;
  background: url('../../images/loader.gif') no-repeat 50% 50%;
  background-size: 40px;
}
.rotate-controls {
  position: absolute;
  bottom: 30px;
  left: 0;
  right: 0;
  text-align: center;
}
.rotate-controls .rotate-left,
.rotate-controls .rotate-right {
  background: url('../../images/rotate.png') no-repeat;
  height: 60px;
  width: 25px;
  background-position: -10px -5px;
  text-indent: -9999px;
  text-align: center;
  position: relative;
  left: 0;
  margin: auto;
  display: inline-block;
  cursor: pointer;
  transition: all 0.3s ease;
}
.rotate-controls .rotate-left:hover,
.rotate-controls .rotate-right:hover {
  transform: scale(1.1, 1.1);
}
.rotate-controls .rotate-right {
  background-position: -125px -5px;
}
.rotate-controls .rotate-text {
  background: url('../../images/rotate.png') no-repeat;
  height: 65px;
  width: 80px;
  background-position: -40px -5px;
  text-indent: -9999px;
  display: inline-block;
}
