.craft-map-container html,
.craft-map-container body {
  padding: 0;
  margin: 0;
}
.craft-map-container body {
  font-family: Arial;
  background: #0aa699;
}
.craft-map-container a {
  color: #fff;
  text-decoration: none;
}
.craft-map-container .menu {
  position: absolute;
  top: 330px;
  left: 20px;
  z-index: 20;
}
.craft-map-container .menu a {
  background: #B00000;
  background: rgba(176, 0, 0, 0.6);
  display: block;
  float: left;
  clear: both;
  line-height: 28px;
  margin-bottom: 4px;
  outline: none;
  padding: 0 15px;
  text-decoration: none;
}
.craft-map-container .preloader {
  background: url(../img/loader.gif) no-repeat center #ffffff;
}
.craft-map-container .popup h3 {
  margin: 0;
  margin-bottom: 10px;
  font-weight: bold;
  font-size: 15px;
  color: #0aa699;
  text-transform: uppercase;
}
.craft-map-container .controls {
  position: absolute;
  bottom: 0px;
  left: 0px;
  z-index: 20;
  width: 100%;
  background: #15aa9e;
}
.craft-map-container .popup {
  width: 300px;
  position: absolute;
  padding: 8px;
  background: #fff;
  font-size: 13px;
  line-height: 20px;
  border-radius: 3px;
}
.craft-map-container .popup a {
  color: black;
}
.craft-map-container .marker {
  display: block;
  text-indent: -9999px;
  width: 15px;
  height: 23px;
  width: 59px;
  height: 61px;
  outline: none;
  background: url(../images/marker.html) no-repeat;
  background: url(../img/mapMarker.png) no-repeat;
  cursor: pointer;
}
.craft-map-container .popup .close {
  display: block;
  text-indent: -9999px;
  position: absolute;
  top: 4px;
  right: 4px;
  width: 16px;
  height: 16px;
  outline: none;
  background: url(../img/close.gif) no-repeat;
  cursor: pointer;
}
.craft-map-container .controls a {
  background: #15aa9e;
  display: inline-block;
  float: left;
  font-size: 11px;
  line-height: 28px;
  margin-bottom: 0px;
  outline: none;
  padding: 15px;
  text-decoration: none;
  color: #fff;
}
.craft-map-container .controls a:hover {
  background: #118b81;
  color: #fff;
}
.craft-map-container .controls .active {
  background: #118b81 !important;
  color: #fff;
}
.craft-map-container .popup {
  padding-bottom: 15px;
  padding-left: 18px;
  padding-top: 16px;
}
.craft-map-container .widget-stats {
  padding-left: 0;
  display: inline-block;
}
.craft-map-container .widget-stats .wrapperstats {
  margin: 5px 0 5px 0;
  border-right: 1px solid #e5e9ec;
  margin-right: 7px;
  padding-right: 7px;
}
.craft-map-container .widget-stats .wrapperstats.transparent {
  border-right: 1px solid #e5e9ec;
}
.craft-map-container .widget-stats .wrapperstats.last {
  border: 0;
}
.craft-map-container .widget-stats .item-title {
  font-size: 12px;
}
.craft-map-container .widget-stats .item-count {
  font-size: 15px;
  color: #000;
  display: block;
  font-weight: bold;
}
.craft-map-container .tooltip-inner {
  padding: 6px 9px;
}
.craft-map-container .progress {
  height: 12px;
  background-color: #ebeef1;
  background-image: none;
  box-shadow: none;
}
.craft-map-container .progress.no-radius {
  border-radius: 0px;
}
.craft-map-container .progress.no-radius .progress-bar {
  border-radius: 0px;
}
.craft-map-container .progress.transparent {
  background-color: rgba(0, 0, 0, 0.28);
}
.craft-map-container .progress.progress-small {
  height: 4px;
}
.craft-map-container .progress.progress-large {
  height: 20px;
}
.craft-map-container .progress-bar {
  box-shadow: none;
  border-radius: 3px;
  background-color: #0090D9;
  background-image: none;
  -webkit-transition: all 1000ms cubic-bezier(0.785, 0.135, 0.15, 0.86);
  -moz-transition: all 1000ms cubic-bezier(0.785, 0.135, 0.15, 0.86);
  -ms-transition: all 1000ms cubic-bezier(0.785, 0.135, 0.15, 0.86);
  -o-transition: all 1000ms cubic-bezier(0.785, 0.135, 0.15, 0.86);
  transition: all 1000ms cubic-bezier(0.785, 0.135, 0.15, 0.86);
  /* easeInOutCirc */
  -webkit-transition-timing-function: cubic-bezier(0.785, 0.135, 0.15, 0.86);
  -moz-transition-timing-function: cubic-bezier(0.785, 0.135, 0.15, 0.86);
  -ms-transition-timing-function: cubic-bezier(0.785, 0.135, 0.15, 0.86);
  -o-transition-timing-function: cubic-bezier(0.785, 0.135, 0.15, 0.86);
  transition-timing-function: cubic-bezier(0.785, 0.135, 0.15, 0.86);
  /* easeInOutCirc */
}
.craft-map-container .progress-bar-success {
  background-color: #0AA699;
  background-image: none;
}
.craft-map-container .progress-bar-info {
  background-color: #0090D9;
  background-image: none;
}
.craft-map-container .progress-bar-warning {
  background-color: #FDD01C;
  background-image: none;
}
.craft-map-container .progress-bar-danger {
  background-color: #F35958;
  background-image: none;
}
.craft-map-container .progress-bar-white {
  background-color: #fff;
  background-image: none;
}
.craft-map-container .progress-bar-black {
  background-color: #1b1e24;
  background-image: none;
}
.craft-map-container .progress-info .bar,
.craft-map-container .progress .bar-info {
  background-color: #0090D9;
  background-image: none;
}
.craft-map-container .progress-warning .bar,
.craft-map-container .progress .bar-warning {
  background-color: #FDD01C;
  background-image: none;
}
.craft-map-container .progress-danger .bar,
.craft-map-container .progress .bar-danger {
  background-color: #F35958;
  background-image: none;
}
.craft-map-container .progress-white .bar,
.craft-map-container .progress .bar-white {
  background-color: #fff;
  background-image: none;
}
.craft-map-container .progress-success.progress-striped .bar,
.craft-map-container .progress-striped .bar-success {
  background-color: #0AA699;
}
.craft-map-container .progress-info.progress-striped .bar,
.craft-map-container .progress-striped .bar-info {
  background-color: #0090D9;
}
.craft-map-container pre .nocode {
  background-color: none;
  color: #000000;
}
.craft-map-container pre .str {
  color: #a4d886;
}
.craft-map-container pre .str,
.craft-map-container pre .lit {
  color: #ffa0a0;
}
.craft-map-container pre .kwd {
  color: #f0e68c;
  font-weight: bold;
}
.craft-map-container pre .typ {
  color: #98fb98;
}
.craft-map-container pre .pun {
  color: #ffffff;
}
.craft-map-container pre .pln {
  color: #ffffff;
}
.craft-map-container pre .dec {
  color: #98fb98;
}
.craft-map-container pre .atn {
  color: #8AC763;
  font-weight: bold;
}
.craft-map-container pre .tag {
  color: #8AC763;
  font-weight: bold;
}
.craft-map-container pre .atv {
  color: #ffa0a0;
}
.craft-map-container ol.linenums {
  margin-top: 0;
  margin-bottom: 0;
  color: #aeaeae;
}
.craft-map-container li.L0,
.craft-map-container li.L1,
.craft-map-container li.L2,
.craft-map-container li.L3,
.craft-map-container li.L5,
.craft-map-container li.L6,
.craft-map-container li.L7,
.craft-map-container li.L8 {
  list-style-type: none;
}
.craft-map-container pre .com {
  color: #A4D886;
  /*  */
  color: #F0E68C;
  /*  */
  color: #87CEEB;
  /*  */
  font-style: normal;
  font-family: arial;
}
.craft-map-container pre.prettyprint.lang-html {
  max-width: 970px;
  /* 
	*/
  overflow: hidden;
}
.craft-map-container pre.prettyprint.lang-html:hover {
  max-width: none;
}
.craft-map-container body.black {
  background: black;
  box-shadow: none !important;
}
.craft-map-container .black pre {
  box-shadow: #555 0px 0px 15px;
  border: 1px solid #666;
}
.craft-map-container .bif {
  box-shadow: inset 0 0 10px #bbb !important;
}
.craft-map-container pre {
  display: inline-block;
  background-color: #222;
  margin-top: 14px;
  margin-bottom: 14px;
  padding-top: 12px;
  padding-left: 12px;
  padding-bottom: 12px;
  color: white;
  -moz-tab-size: 4;
  -webkit-tab-size: 4;
  -o-tab-size: 4;
  tab-size: 4;
  padding-right: 20px !important;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  font-size: 14px;
}
.craft-map-container html {
  height: 100%;
}
.craft-map-container h1 {
  font-size: 25px;
  margin-top: 0;
  line-height: 1.2em;
  margin-bottom: 15px;
  margin-top: 15px;
  font-weight: normal;
}
.craft-map-container a {
  color: #0077B3;
}
.craft-map-container a:visited {
  color: #0077B3;
}
.craft-map-container body,
.craft-map-container html {
  font-family: arial;
  font-size: 14px;
}
.craft-map-container p {
  margin-top: 15px;
  margin-bottom: 15px;
  line-height: 1.3em;
}
.craft-map-container .none {
  display: none !important;
}
.craft-map-container .dashed {
  text-decoration: none;
  border-bottom: 1px dashed;
}
.craft-map-container .backLink:hover {
  color: #bc0000;
}
.craft-map-container .demoAdsBox {
  float: left;
}
.craft-map-container .footer,
.craft-map-container .dfooter {
  font-family: arial;
  zoom: 1;
  margin-top: -150px;
  height: 150px;
  position: relative;
  padding-left: 30px;
}
.craft-map-container .clear {
  clear: both !important;
}
.craft-map-container .backLink {
  line-height: 1.2em;
  float: left;
  padding-top: 5px;
  margin-right: 30px;
  margin-bottom: 15px;
  color: #0077B3;
  font-size: 25px !important;
  font-family: arial;
  padding: 5px;
}
.craft-map-container .dlangBox {
  font-weight: bold;
}
.craft-map-container .dlangBox a {
  font-weight: normal;
}
.craft-map-container .dlangBox span {
  margin-right: 7px;
}
.craft-map-container .dnavBox a {
  color: #777;
}
.craft-map-container .jqVersionDescr {
  margin-top: 5px;
  font-size: 12px;
}
.craft-map-container .jqBoxClose {
  position: absolute;
  top: 2px;
  right: 4px;
  text-decoration: none;
  font-weight: bold;
  color: black;
  font-size: 14px;
  line-height: 1em;
}
.craft-map-container .dnavBox {
  position: absolute;
  top: 10px;
  right: 17px;
  line-height: 1.3em;
  font-size: 11px;
  color: #777;
  z-index: 100;
}
.craft-map-container .jqBox {
  position: absolute;
  top: 25px;
  right: -5px;
  background: white;
  border: 1px solid #bbb;
  padding: 15px;
  z-index: 200;
  color: black;
  -webkit-box-shadow: rgba(0, 0, 0, 0.3) 0 0 21px;
  -moz-box-shadow: rgba(0, 0, 0, 0.3) 0 0 21px;
  box-shadow: rgba(0, 0, 0, 0.3) 0 0 21px;
}
.craft-map-container .jqBoxTitle {
  font-size: 13px;
  white-space: nowrap;
  margin-bottom: 8px;
}
.craft-map-container .buttonsLine form {
  margin: 0;
  padding: 0;
  line-height: 1em;
}
.craft-map-container .buttonsLine form input {
  margin: 0;
  padding: 1px;
  min-width: 44px;
  display: inline-block;
  text-align: center;
}
.craft-map-container .jqueryCurrent {
  font-size: 18px;
  font-weight: normal;
  padding-left: 3px;
  line-height: 1em;
  padding-top: 2px;
  padding-bottom: 2px;
  padding-left: 2px;
  padding-right: 2px;
  display: inline-block;
  background: #FBEF9A;
}
.craft-map-container .buttonsLine form,
.craft-map-container .jqueryCurrent {
  margin-bottom: 4px;
}
.craft-map-container body {
  background: #0aa699;
  padding: 0;
  margin: 0;
  height: 100%;
}
.craft-map-container .wrapper,
.craft-map-container .demowrap {
  min-height: 100% !important;
  padding-left: 30px;
}
.craft-map-container body.nopadding div.demowrap {
  padding-left: 0;
}
.craft-map-container .inner,
.craft-map-container .dinner {
  padding-top: 1px;
  padding-bottom: 200px;
}
.craft-map-container .nopadding .demowrap {
  padding: 0 !important;
}
.craft-map-container .FSrecommended {
  font-size: 18px;
  margin-top: 30px;
  display: inline-block;
  border: 1px dashed green;
  padding: 7px 12px;
  margin-bottom: 20px;
}
.craft-map-container .vertical-tabs {
  margin-bottom: 40px;
}
.craft-map-container .noFooter {
  margin-bottom: 50px;
}
.craft-map-container .backButtonAbsolute {
  position: absolute;
  bottom: 10px;
  left: 10px;
  display: inline;
  z-index: 1000;
  background: #eee;
  margin: 0 !important;
  opacity: 0.8;
  filter: alpha(opacity=80);
}
